import { Formik } from 'formik';
import * as Yup from 'yup';
import { useDispatch } from 'react-redux';
import { logIn } from 'Redux/auth/operations';
import { useState } from 'react';
import 'react-toastify/dist/ReactToastify.css';
import { AuthInput } from 'components/AuthInput/AuthInput';
import { Button } from 'components/Global/Button/Button';
import { ButtonLink } from 'components/Global/ButtonLink/ButtonLink';
import {
  EnterTitle,
  EyeBtn,
  GoogleButton,
  InputList,
  RegistTxt,
  StyledForm,
  StyledHideIcon,
  StyledShowIcon,
  Text,
  UnderBtnBox,
  UnderBtnTxt,
} from 'components/EnterBox/EnterBox.styled';

const validationSchema1 = Yup.object({
  credential: Yup.string().required("  '"),
  password: Yup.string()
    .min(6, '     6  18 ')
    .max(18, '     6  18 ')
    .matches(
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/,
      '   :   ,   ,  .'
    )
    .required("  '"),
});

export const Auth = ({ onToggleClick }) => {
  const [showPassword, setShowPassword] = useState(false);
  const dispatch = useDispatch();

  return (
    <>
      <EnterTitle>  </EnterTitle>
      <Formik
        initialValues={{
          credential: '',
          password: '',
        }}
        validationSchema={validationSchema1}
        onSubmit={(values, actions) => {
          dispatch(
            logIn({
              credential: values.credential,
              password: values.password,
            })
          );
        }}
      >
        <StyledForm>
          <InputList>
            <AuthInput
              isRequired={true}
              type="credential"
              placeholder="Email  Username"
              name="credential"
            />
            <AuthInput
              isRequired={true}
              type={showPassword ? 'text' : 'password'}
              placeholder=""
              name="password"
            >
              <EyeBtn
                type="button"
                onClick={() => setShowPassword(!showPassword)}
              >
                {showPassword ? <StyledShowIcon /> : <StyledHideIcon />}
              </EyeBtn>
            </AuthInput>
          </InputList>
          <Button type="submit"></Button>
        </StyledForm>
      </Formik>

      <ButtonLink> ?</ButtonLink>

      <Text>
        <span></span>
      </Text>

      <GoogleButton>  Google</GoogleButton>

      <UnderBtnBox>
        <UnderBtnTxt> ?</UnderBtnTxt>
        <RegistTxt
          onClick={() => {
            onToggleClick();
          }}
        >
          
        </RegistTxt>
      </UnderBtnBox>
    </>
  );
};
